import  React from 'react';
import ReactDOM from 'react-dom';
import ComponentFirstNew from "./firstNewsWord"
import  Gallery from  './masonry'
import Elevator from './Elevator'
export default class ComonentBody extends React.Component{
    constructor(){
        super()
        this.state = {
            flagLoading:"false"


        }
    }
    componentDidMount(){
        $(window).scroll(function (e) {
            if((($(window).scrollTop()+$(window).height())+2250)>=$(document).height()){


              console.log('badi')
                this.setState({
                    flagLoading:'loading'
                })
            }


        })
    }
    render(){

        return(

            <div className="wrapper">
             {/*   <ComponentFirstNew/>*/}
                <Gallery elements={this.state.elements}/>
                <div className={this.state.flagLoading}>正在加载</div>
                <Elevator/>
           </div>
        )
    }
}




